import {Component, OnInit} from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {first} from 'rxjs/operators';

import {AuthenticationService} from 'src/app/_services/admin.authentication.service';

@Component({
    selector: 'app-admin-add-code',
    templateUrl: './admin-add-code.component.html',
    styleUrls: ['./admin-add-code.component.css']
})
export class AdminAddCodeComponent implements OnInit {
    signupForm: FormGroup;
    loading = false;
    submitted = false;
    error = '';
    success = false;

    constructor(
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private router: Router,
        private authenticationService: AuthenticationService
    ) {
        // if (this.authenticationService.currentUserValue) {
        //   this.router.navigate(['/']);
        // }
    }

    ngOnInit(): void {
        const registerCodeReg = /([A-Za-z0-9]{5}-){4}[A-Za-z0-9]{5}$/;
        this.signupForm = this.formBuilder.group({
            code: new FormControl('', Validators.compose([
                Validators.required, Validators.pattern(registerCodeReg)]))
        });
    }

    // convenience getter for easy access to form fields
    // tslint:disable-next-line:typedef
    get f() {
        return this.signupForm.controls;
    }

    // tslint:disable-next-line:typedef
    onSubmit() {
        this.success = false;
        this.submitted = true;

        // stop here if form is invalid
        if (this.signupForm.invalid) {
            return;
        }

        this.loading = true;
        this.authenticationService.addCode(this.f.code.value)
            .pipe(first())
            .subscribe({
                next: () => {
                    this.error = '';
                    this.success = true;
                    this.loading = false;
                },
                error: error => {
                    this.error = error;
                    this.loading = false;
                }
            });
    }

    removeIcon(): void {
        this.error = '';
        this.success = false;
    }

    generate(): void {
        const registerCodeReg = /([A-Za-z0-9]{5}-){4}[A-Za-z0-9]{5}$/;
        const code = this.createCode();
        this.signupForm = this.formBuilder.group({
            code: new FormControl(code, Validators.compose([
                Validators.required, Validators.pattern(registerCodeReg)]))
        });
        this.removeIcon();
    }

    createCode(): string {
        let code = '';
        for (let i = 0; i < 4; i ++) {
            code += this.create_pwd() + '-';
        }
        code += this.create_pwd();
        return code;
    }

    create_pwd(): string {
        const pwdLength = 5;
        let newPass = '';
        const char = 'ABCDEFGHIJKLMNPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz1234567890';
        for (let i = 0; i < pwdLength; i++) {
            const j = Math.floor(Math.random() * char.length);
            newPass += char.charAt(j);
        }
        return newPass;
    }
}
